import React from 'react';

import List from '@material-ui/core/List';
import ListSubheader from '@material-ui/core/ListSubheader';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import Checkbox from '@material-ui/core/Checkbox';


class BooleanOption extends React.PureComponent {
  render() {
    return (
      <ListItem
	button
	onClick={this.props.onClick}
      >
	<Checkbox
	  checked={this.props.checked}
	/>
	<ListItemText>{this.props.label}</ListItemText>
      </ListItem>
    );
  }
}

class OptionsPanel extends React.PureComponent {
  render() {
    return (
      <List id='options-panel'>
	<ListSubheader disableSticky>Options</ListSubheader>
	<BooleanOption
	  onClick={() => this.props.onToggle('enable_checkerboard')}
	  checked={this.props.variables.enable_checkerboard > 0.5}
	  label='Enable Checkerboard'
	/>
	<BooleanOption
	  onClick={() => this.props.onToggle('invert_gradient')}
	  checked={this.props.variables.invert_gradient > 0.5}
	  label='Invert Gradient'
	/>
	<BooleanOption
	  onClick={() => this.props.onToggle('antialiasing')}
	  checked={this.props.variables.antialiasing > 0.5}
	  label='Enable Antialiasing'
	/>
      </List>
    );
  }
}

export default OptionsPanel;



